import PropTypes from 'prop-types'
import React from 'react'
import styled from 'styled-components'

const StyledHeading = styled.h1`
   margin: 0;
   font-weight: ${({ size, theme }) =>
      size === 'h1' ? theme.fontWeight.bold : theme.fontWeight.regular};

   @media ${({ theme }) => theme.breakpoints.min.mobile} {
      font-size: ${({ size, theme }) => {
         switch (size) {
            case 'h2':
               return theme.fontSize.l
            case 'h3':
            case 'h4':
               return theme.fontSize.m
            default:
               return theme.fontSize.xl
         }
      }};
   }
   @media ${({ theme }) => theme.breakpoints.max.mobile} {
      font-size: ${({ size, theme }) => {
         switch (size) {
            case 'h2':
               return theme.fontSize.l
            case 'h3':
            case 'h4':
               return theme.fontSize.m
            default:
               return '12vw'
         }
      }};
   }
   color: ${({ size, theme }) => size === 'h4' && theme.fontSecondary};
   text-align: center;
`

const Heading = ({ children, size, ...props }) => (
   <StyledHeading as={size} size={size} {...props}>
      {children}
   </StyledHeading>
)

Heading.propTypes = {
   children: PropTypes.string,
   size: PropTypes.oneOf(['h1', 'h2', 'h3', 'h4']),
}

Heading.defaultProps = {
   children: '',
   size: 'h1',
}

export default Heading
